.Profile {
    width: 280px;
    margin: 0 auto;
    font-family: Arial, Helvetica, sans-serif;
    border: 1px solid #c0c0c0;
  }
  .Description {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
  }
  .Avatar {
    padding: 30px;
    border-radius: 50%;
  }
  .Name {
    margin: 0;
    font-size: 30px;
    font-weight: 600;
  }
  .Tag,
  .Location,
  .Label {
    margin: 5px 0;
    color: #989898;
  }
  .Stats {
    padding: 0;
    margin-bottom: 0;
    display: flex;
    list-style: none;
    justify-content: space-between;
    background-color: #c0c0c0;
  }
  .Stats__item {
    width: 33%;
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ececec;
    border: 1px solid #c0c0c0;
  }
  
  .Quantity {
    font-family: sans-serif;
    font-weight: 600;
  }